h1{
    color: white;
    background: transparent;
    font-size: 50px;
   
    padding: 6px 0px 2px 0px;
    margin-bottom: 10px;
  

}
.header{
    width: 100%;
    line-height: 100px;
    background: rgb(36,0,0);
    background: linear-gradient(90deg, rgba(36,0,0,1) 0%, rgba(121,9,9,1) 6%, rgba(255,0,61,1) 100%);
    display: flex;
    justify-content: start;
    align-items: center;
    padding-left: 50px;
    color: white;
    box-shadow: 5px 3px 15px -5px rgba(0,0,0,0.3);
}

.header h1{
    padding-left: 20px;
}

.header img{
  
    
    width: 70px;
    height: 70px;
    display: flex;
    justify-content: start;
    align-items: center;
 
}

.main_note{
    width: 500px;
    padding: 15px 10px 5px 10px;
    margin: 20px auto;
}

.my_notes h1{
    color: #34495e;
}

.my_notes p {
    color: gray;
}

form{
    min-height: 50px;
    color: #ecf0f1;
    padding: 15px 10px 5px 10px;
    box-shadow: 3px 5px 15px -5px rgba(0,0,0,0.3);
    position: relative;
}

input{
    width: 100%;
    line-height: 30px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 1.5rem;
    margin: 10px 0;
    font-weight: bold;
}

textarea{
    width: 100%;
    line-height: 30px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 1rem;
    margin: 10px 0;
}

.MuiButton-root {
    width: 30px;
    height: 50px;
    position: absolute;
    bottom: -28px;
    left: 82%;
    cursor: pointer;
    background-color: #fff;
    box-shadow: 5px 5px 15px rgba(0,0,0,0);
    border-radius: 50%;

}

.MuiButton-root .plus_sign{
    font-size: 45px;
    font-weight: 900;
    color: #a40000;
    cursor: pointer;
    z-index: 222;
    background: transparent;
    width: min(50px, 90%, 70vw);
    
}

.MuiButton-root:hover {
    background: #a40000;
}

.MuiButton-root:hover .plus_sign {
    color: #270000;
    background: transparent;
}

.note {
    background: #fff;
    border-radius: 7px;
    box-shadow: 0 2px 5px #ccc;
    padding: 10px;
    width: 240px;
    margin: 16px;
    float: left;
}

.note h1{
    font-size: 1.1em;
    margin-bottom: 6px;
    color: rgba(121,9,9,1);
}

.note p{
    font-size: 1.1em;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
}

.note .btn {
    position: relative;
    float: right;
    margin-right: 10px;
    color: #a40000;
    border: none;
    width: min(50px, 90%, 70vw);
    height: 50px;
    background: transparent;
    cursor: pointer;
    outline: none;
    box-shadow:  3px 3px 15px -10px rgba(0,0,0,0.3);
    border-radius: 50%;
}

.deleteIcon{
    width: 0.7em;
    height: 0.7em;
}

.note .btn:hover{
    background-color: #a40000;
}

.note .btn:hover .deleteIcon{
    color: #fff;
}


footer{
    width: 100%;
    bottom: 0;
    position: absolute;
    /* align-items: center; */
   
    /* padding-left: 50px; */
    text-align: center;
    background: rgb(238,174,202);
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);

}

 

@media (max-width:767px) {
        .main_note{
            width: 90%;
        }
        .MuiButton-root .plus_sign{
          position: absolute;
        }
        footer{
            width: 100%;
            position: absolute;
            text-align: center;
        }
        
        
